.hero {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: auto;
	position: relative;

	&__container {
		width: 100%;
		height: 100vh;
		margin: 0;
		display: block;
		position: relative;
		overflow: hidden;
		z-index: 0;
	}

	&__content {
		position: absolute;
		background: rgba($color: $background-color, $alpha: 0.8);

		height: 100%;
		width: 100%;
		color: $font-color;
		display: flex;
		flex-flow: column nowrap;
		justify-content: center;
		align-items: center;

		&__headline {
			margin: 2rem 2rem;
			font-size: 4rem;
			font-weight: 900;
		}

		&__copy {
			margin: 2rem 2rem;

			font-size: 2rem;
			font-weight: 300;
		}
		&__name-span {
			position: absolute;
			font-family: "Yellowtail", cursive;

			top: -120px;
			left: 0;
			display: block;
			color: $secondary-color;
			font-size: 5rem;
			transform: rotate(-13deg);
		}
	}

	&__name-span-container {
		position: relative;
		width: 100%;
		margin-top: 20rem;
	}
}

#hero-video {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	-o-object-fit: cover;
	object-fit: cover;
	z-index: 0;
}

@media (min-width: $breakpoint-tablet) {
	.hero {
		&__content__headline {
			margin: 2rem 4rem;
		}
	}
}
