.contact {
	&__form {
		display: flex;
		flex-flow: column nowrap;
		justify-content: space-evenly;
		align-items: center;
	}

	&__form-divider {
		margin-top: 1rem;
		width: 100%;
	}

	&__form-divider label {
		display: block;
		font-size: 2.5rem;
		text-transform: uppercase;
	}
	&__form-divider input {
		width: 95%;
		display: block;
		margin-top: 2rem;
		padding: 1rem;
		background: none;
		font-family: "Lato";
		font-size: 2rem;
		border: none;
		border-bottom: 1px solid $secondary-color;
		transition: all 0.2s ease-out;
		box-shadow: 0 1rem 2rem rgba($dark-font-color, 0.1);
		color: $font-color;
	}
	&__form-divider input:focus {
		border-bottom: 4px solid $secondary-color;
		outline: none;
	}

	&__form-divider textarea {
		width: 95%;
		margin-top: 2rem;

		height: 120px;
		border: 1px solid $secondary-color;
		padding: 1rem;
		background: none;
		font-family: "Lato";
		font-size: 2rem;
		transition: all 0.2s ease-out;
		color: $font-color;
		&:focus {
			outline: none;
			border: 3px solid $secondary-color;
		}
	}
}
