$color-primary: #51ff00;

.btn {
  border-radius: 50px;
  border: none;
  width: 20rem;
  height: 5rem;
  color: white;
  font-family: sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  text-shadow: 2px 2px rgba($color: #000000, $alpha: 0.2);
  line-height: 33px;
  margin: 1rem;
  box-shadow: 2px 2px 5px rgba($color: #000000, $alpha: 0.4);

  /* identical to box height */

  text-align: center;

  &:hover {
    transform: translateY(-3px);
    box-shadow: 4px 4px 5px rgba($color: #000000, $alpha: 0.2);
  }
  &:active {
    transform: translateY(3px);
    box-shadow: 2px 2px 5px rgba($color: #000000, $alpha: 0.6);
  }
  &.secondary {
    background: $secondary-color;
    color: $dark-font-color;
    text-shadow: $text-shadow;
  }
  &.clear {
    background: transparent;
    border: 1px solid white;
    -webkit-text-fill-color: transparent; /* Will override color (regardless of order) */
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
  }
  &.white {
    color: black;
  }
  &.sm {
    width: fit-content;
    margin-left: 0;
    padding: 0 1rem;
    height: fit-content;
    font-size: 2rem;
    border-radius: 5px;
  }
}

/* Title Utl */
.title {
  width: 100%;
  padding: 2rem 0;
  margin-top: 10rem;
  text-align: center;
  display: flex;
  font-weight: normal;
  flex-flow: row nowrap;
  justify-content: space-between;
  font-family: 'Picadilly', sans-serif;
  z-index: 10;
  &__letter {
    font-size: 8rem;
    color: $neon-color;
    font-weight: normal;
  }
}

.tilt-left {
  transform: rotate(-3deg);
}

.tilt-right {
  transform: rotate(2.5deg);
}
.falling-left {
  transform: translateY(10%) rotate(-5.2deg);
  opacity: 0.5;
}
// Pink Neon

.pink-neon {
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff,
    0 0 42px rgb(255, 101, 229), 0 0 82px rgb(255, 70, 224),
    0 0 92px rgb(255, 57, 222), 0 0 102px rgb(255, 30, 218),
    0 0 120px rgb(255, 0, 212);
}

.flicker-slow {
  animation: flicker 3s linear infinite;
}

.flicker-fast {
  animation: flicker 1s linear infinite;
}

.blink {
  animation: blinkAnimation 3s linear infinite;
}

.separator {
  align-self: center;
  width: 100%;
  background-color: rgb(255, 101, 229);
  height: 2px;
  transform: translateY(-10px);

  box-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff,
    0 0 42px rgb(255, 101, 229), 0 0 82px rgb(255, 70, 224),
    0 0 92px rgb(255, 57, 222), 0 0 102px rgb(255, 30, 218),
    0 0 120px rgb(255, 0, 212);
}

@media (min-width: $breakpoint-tablet) {
  .title {
    width: 100%;
    padding: 2rem 0;
    margin-top: 10rem;
    text-align: center;
    justify-content: center;

    &__letter {
      font-size: 10rem;
      color: $neon-color;
      font-weight: normal;
    }
  }
}
